.head-nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap;
    width: 20%;
    min-width: 13rem;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-left: 0;
    margin-bottom: .5rem;
    position: absolute;
    background: #0070af;
    left: 6rem;
    top: 6rem;
    text-align: center;
    border: 1px solid white;
}

.head-nav-menu__button {
    width: 1.5rem;
}

.head-nav-menu-item{
    position: relative;
    margin: .5rem 0;
}

.head-nav-menu__link:before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: .05rem;
    background: rgba(255, 255, 255);
    opacity: 0;
    bottom: -.3rem; 
    transition: opacity .1s linear;
}

.head-nav-menu__link:hover:before {
    opacity: 1;
}

@media (min-width: 768px){
    .head h1 {
        text-align: left;
    }

    .head-nav-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-flow: row wrap;
                flex-flow: row wrap;
        width: 100%;
        position: relative;
        left: 0;
        top: 0;
        text-align: start;
        border: none;
    }
    .head-nav-menu__button {
       display: none;
    }
}


